import { Component, h, Element, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-enable-section',
})
export class OrderEnableection {

    @Prop() heading: string;
    @Prop() name: string;
    @Prop() value: string;

    @Element() root: HTMLElement;

    @Prop({mutable: true, reflect: true}) active: boolean = false;
    ChangeHandler() {
        const input = this.root.querySelector(`div > ks-input-check input`) as HTMLInputElement;
        this.active = input.checked;
    }

    componentDidLoad(){
        this.ChangeHandler();
    }

    render() {
        return (
            <div>
                <ks-input-check name={this.name} value={this.value} onChange={()=>this.ChangeHandler()} label={this.heading}/>

                <div class="inputs" hidden={!this.active}>
                    <slot />
                </div>
            </div>
        )
    }
}